import { Topic_TYPES } from '../../topicType';

const initialDataList = {
    dataList: [],
    tab: Topic_TYPES[0].value,  //ask
    limit: 10,
    govalue: 1,
    pageCurrent: 1,
    idList: [],
    id: 0,
};

const reducer = (state = initialDataList, action) => {
    switch (action.type) {
        case 'SET_DATALIST':
            return Object.assign(
                {},
                state,
                {
                    dataList: action.dataList,
                }
            );
        case 'SET_PAGECURRENT':
            return Object.assign(
                {},
                state,
                {
                    pageCurrent: action.pageCurrent
                }
            );
        case 'SET_TABS':
            return Object.assign(
                {},
                state,
                {
                    tab: action.tab
                }
            );
        case 'SET_LIMIT':
            return Object.assign(
                {},
                state,
                {
                    limit: action.limit
                }
            );
        case 'SET_GOVALUE':
            return Object.assign(
                {},
                state,
                {
                    govalue: action.govalue
                }
            );
        case 'SET_IDLIST':
            return Object.assign(
                {},
                state,
                {
                    idList: action.idList
                }
            );
        case 'SET_ID':
            return Object.assign(
                {},
                state,
                {
                    id: action.id
                }
            );
        default: return state;
    }
};

export default reducer;






