import { fetchDetailData } from './services'
import React, { Component } from 'react'
import ReactMarkdown from 'react-markdown'
import './App.css';
import {fetchIndexData} from './services';


import { connect } from 'react-redux'
import {setId, setDataList,setPageCurrent,setLimit,setIdList} from './redux/topic/actions'
class DetailPage extends Component {
  constructor() {
    super();
    this.fecthDataById = this.fecthDataById.bind(this);
    this.fetchNewData = this.fetchNewData.bind(this);
    this.state = {
      data: {},
      comment: []
    }
  }
  componentDidMount() {
    let id = this.props.match.params.id; //获取文章对应的id     

    fetchDetailData(id).then((res) => {
      this.setState({
        data: res.data,
        comment: res.data.replies
      })
      // console.log("res is:", res);
      // console.log("data is:", res.data);
    });

  }

  fecthDataById(index){
    let id = this.props.idList[index];
    console.log('id is',id);
    fetchDetailData(id).then((res) => {
      this.setState({
        data: res.data,
        comment: res.data.replies
      })
      console.log("res is:", res);
      console.log("data is:", res.data);
    });
    this.props.history.push(`/details/${id}`)
  }

  fetchNewData(pageCurrent,newIndex){
    const {tab,limit} = {...this.props};
    
    fetchIndexData({tab,pageCurrent,limit}).then((res)=>{
      const idlist = [];
      res.data.forEach(element => {
          idlist.push(element.id);
      });

      this.props.onChangeData(res.data,pageCurrent,limit,idlist);

      this.fecthDataById(newIndex);
      this.props.ChangeId(this.props.idList[newIndex]);

    },(e)=>{
        window.alert('error'+e.message);
    });
  }

  prevPage(){
    let id = this.props.match.params.id; //获取文章对应的id   
    let index = this.props.idList.indexOf(id) - 1;
    if( this.props.idList.indexOf(id) > 0 ){
      this.fecthDataById(index);
      this.props.ChangeId(this.props.idList[index]);
    }else{
      if(this.props.pageCurrent === 1){
        alert('当前已是第一篇文章！');
      }else{
        let pageCurrent = this.props.pageCurrent - 1;
        let newIndex = 9 ;

        this.fetchNewData(pageCurrent,newIndex);
      }
    }
  }
  nextPage(){
    let id = this.props.match.params.id; //获取文章对应的id   
    let index = this.props.idList.indexOf(id) + 1;
    if(this.props.idList.indexOf(id) < 9){
      this.fecthDataById(index);
      this.props.ChangeId(this.props.idList[index]);
    }else{
      let pageCurrent = this.props.pageCurrent + 1;
      let newIndex = 0 ;

      this.fetchNewData(pageCurrent,newIndex);
    }
  }
  
  render() {

    let list = this.state.comment.map((element) => {
      return (
        <div className="comment-list">
          <img src={element.author.avatar_url} alt="userPhoto" />
          <div className="comment-list-content">
            <div className="comment-list-title">{element.author.loginname} </div>
            <div key={element.id} dangerouslySetInnerHTML={{ __html: element.content }}></div>
          </div>
        </div>
      );
    })

    return (
      <div className="detail-wrap">
        <div className="details">
          <div className="title">{this.state.data.title}</div>
          <div id="content">
            <ReactMarkdown source={this.state.data.content} />
            <div className="changeDetail">
              <span className="prev" onClick={this.prevPage.bind(this)}>上一篇</span>
              <span  className="next" onClick={this.nextPage.bind(this)}>下一篇</span>
            </div>
          </div>
          <Back />
        </div>
        <div className="comment">
          <div className="comment-sum">共{this.state.data.reply_count}回复</div>
          {list}
        </div>
      </div>
    );
  }
}
class Back extends Component {
  handleBack(e) {
    e.preventDefault();
    window.history.back();
  }
  render() {
    return (
      <div className="backToList" onClick={this.handleBack.bind(this)}>回到列表</div>
    );
  }
}

const mapStateToProps = ({Topic}) =>{
  return {
    pageCurrent: Topic.pageCurrent,
    tab: Topic.tab,
    dataList: Topic.dataList,
    idList: Topic.idList,
    limit: Topic.limit,
  }
}
const mapDispatchToProps = (dispatch) => {
return {
  ChangeId: (id) => {
      dispatch(setId(id)); 
  },
  onChangeData: (dataList,pageCurrent,limit,idList) => {
    dispatch(setDataList(dataList));
    dispatch(setPageCurrent(pageCurrent));
    dispatch(setLimit(limit));
    dispatch(setIdList(idList));
  },
  // changePag: (pagenum)=>{
  //   dispatch(setPageCurrent(pagenum))
  // }
}
}
DetailPage = connect(mapStateToProps, mapDispatchToProps)(DetailPage)

export default DetailPage


