import React, { Component } from 'react';
import './App.css';

import { connect } from 'react-redux'
import {setGoValue} from './redux/topic/actions'

class PageButton extends Component {
    constructor(props) {
        super(props);
        this.handleOnChange = this.handleOnChange.bind(this);
        this.handlePageChange = this.handlePageChange.bind(this);
        this.handleLimitChange = this.handleLimitChange.bind(this);
        this.goPage = this.goPage.bind(this);
    }
    //点击按钮就调用父组件handlePageChange跳转到相应页面
    handlePageChange = (pageNumber) => {
        if(this.props.handlePageChange){
            this.props.handlePageChange({
                pageCurrent: +pageNumber,
                limit: this.props.limit,
            });
        }
    }

    //limit修改后，调用父组件handlePageChange修改当前对应内容
    handleLimitChange = (params) => {
        const { pageCurrent, limit } = { ...this.props, ...params};
       
        if(this.props.handlePageChange){
            this.props.handlePageChange({
                pageCurrent,
                limit,
            });
        }
    }

    //输入框值改变时，改变this.state.value
    handleOnChange(e){
        if(e.target.value > 0){
            this.props.onChangePage(e.target.value);
        }else{
            alert("请输入正确范围内的值！")
        }    
    }
    //跳转到某页
    goPage(){
        if(this.props.govalue){
            this.handlePageChange( this.props.govalue );
            // console.log("value is ", store.govalue);
        }
    }

    render() {
        const { pageCurrent = 1, limit = 10, hasNext = true} = this.props; //当前页
        //hasNaxt用于判断    是否还有下文
        const pageItemList = [];

        for(let i = Math.max(pageCurrent - 3, 1); i< pageCurrent; i++){
            pageItemList.push(<button className="button-item" onClick={()=>this.handlePageChange(i)} key={`${i}`}>{i}</button> );
        }

        pageItemList.push(<button className="button-item not-click" key={`${pageCurrent}`}>{ pageCurrent }</button>); //当前页
      
        if(hasNext){
            for(let i = +pageCurrent + 1; i <= +pageCurrent + 3; i++) {
                pageItemList.push( <button className="button-item" onClick={()=>this.handlePageChange(i)} key={`${i}`}>{i}</button> );
            }
        }
        
        return (
            <div className="change_page">
                {/* <span onClick={ this.setUp } >上一页</span> */}
                { pageCurrent > 1 && 
                    <button className="button-item pre" onClick={()=>this.handlePageChange(+pageCurrent - 1)}>
                        上一页
                    </button>
                }
                {pageItemList}
                { hasNext && 
                    <button className="button-item next" onClick={()=>this.handlePageChange(+pageCurrent + 1)}>
                        下一页
                    </button>
                }
                 &nbsp;
                分页大小：
                <select value={`${limit}`} onChange={e=>this.handleLimitChange({limit: + e.target.value})} >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={30}>30</option>
                    <option value={40}>40</option>
                </select>
                
                 &nbsp;
                跳转到：<input onChange={this.handleOnChange} type="number" />
                       <button className="go" onClick={this.goPage}> Go</button> 
                {/* <span onClick={ this.setNext }>下一页</span> */}
            </div>
        );
    }
}

const mapStateToProps = ({Topic}) =>{
    return {
      pageCurrent: Topic.pageCurrent,
      limit: Topic.limit,
      govalue: Topic.govalue,
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
      onChangePage: (value) => {
        dispatch(setGoValue(value));
      }
    }
  }

PageButton = connect(mapStateToProps, mapDispatchToProps)(PageButton)

export default PageButton



