import {fetchIndexData} from './services';
import React, { Component } from 'react';
import './App.css';
import {Link} from 'react-router-dom'
import { Topic_TYPES } from './topicType';
import PageButton from './PageButton'

import { connect } from 'react-redux'
import {setPageCurrent,setDataList,setLimit,setIdList, setTabs} from './redux/topic/actions'

class Topic extends Component{
    constructor(){
        super();
        this.handleFilterChange = this.handleFilterChange.bind(this);
        this.handlePageChange = this.handlePageChange.bind(this);
        this.fetchData = this.fetchData.bind(this);
      }
    componentDidMount(){
          const pageCurrent = this.props.match.params.pageCurrent || this.props.pageCurrent;
          const limit = this.props.match.params.limit || this.props.limit;
          const tab = this.props.match.params.tab || this.props.tab;

        //   console.log(pageCurrent,limit,tab);
          this.fetchData({pageCurrent,limit,tab});
         
    }
    
    //根据参数获取数据
    fetchData(params){
        const {tab,pageCurrent,limit} = {...this.props, ...params};
       
        fetchIndexData({tab,pageCurrent,limit}).then((res)=>{
            const idlist = [];
            res.data.forEach(element => {
                idlist.push(element.id);
            });
            
            this.props.onChangeData(res.data,pageCurrent,limit,idlist);

            this.props.history.push(`/topics/${tab}/${pageCurrent}/${limit}`); //将页面的路由信息同步到url

            // console.log("接受到的数据为：",res.data)

        },(e)=>{
            window.alert('error'+e.message);
        });
    }
    handleFilterChange = (type) => {
        this.fetchData({tab: type, pageCurrent:1});
        this.props.onChangeTab(type);
    }

    //根据pageCurrent，limit获取内容
    handlePageChange({pageCurrent,limit}){
        this.fetchData({pageCurrent,limit});
    }

      render() {
        let list = this.props.dataList.map((element,index) => {
            return <li key={index} ><Link to={`/details/${element.id}`}>{index + 1}、{element.title}</Link></li>;
        });
        console.log('this.props.tab:',this.props.tab)
        return (
            <div className="container">
                 <div className="main">
                    {/* tab栏 */}
                    <div className="filter">
                    {
                        Topic_TYPES.map(
                            item => (
                                <div
                                className={"filter-item" + (this.props.tab === item.value ? ' active' : '') } 
                                key={`KEY_${item.value}`}
                                onClick = {() => this.handleFilterChange(item.value)}
                                >
                                {item.name}
                                </div>
                            )
                        )
                    }
                    </div>
                    <ul className="main-list">
                        {list}
                    </ul>
                    <PageButton  handlePageChange={this.handlePageChange}/>
                </div>
            </div>
        );
      }
}

const mapStateToProps = ({Topic}) =>{
      return {
        pageCurrent: Topic.pageCurrent,
        limit: Topic.limit,
        tab: Topic.tab,
        dataList: Topic.dataList,
        idList: Topic.idList,
      }
  }
  const mapDispatchToProps = (dispatch) => {
    return {
      onChangeData: (dataList,pageCurrent,limit,idList) => {

            dispatch(setDataList(dataList));
            dispatch(setPageCurrent(pageCurrent));
            dispatch(setLimit(limit));
            dispatch(setIdList(idList));
      },
      onChangeTab: (value) => {
        dispatch(setTabs(value));
      }
    }
  }
  Topic = connect(mapStateToProps, mapDispatchToProps)(Topic)

  export default Topic


